import { Client } from "@notionhq/client"

import { useState , useEffect, useRef } from 'react';

import { NOTION_API_KEY, NOTION_PROJECT_DATABASE, PAGE_ID } from "../config"

import API from '../APINotion';

const notion = new Client({NOTION_API_KEY});

const databaseId = NOTION_PROJECT_DATABASE;

const initialState = {
    page: 0,
    results: [],
    firstGame: [],
    firstGameId: 0,
    firstGameName: "",
    firstGameSummary: "",
    heroImage: "",
    total_pages: 0,
    total_results: 0
};


export const useNotionFetch = () => {
    const [searchTerm, setSearchTerm] = useState('');
    const [state, setState] = useState(initialState);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);

    const fetchProjects = async (page, searchTerm = "") => {
        try{
            
            setError(false);
            setLoading(true);
            
            const projects = await API.fetchProjects();
            
            setState(prev => ({
                ...projects.results, 
                results:
                    [...projects.results],
            }));

        } catch (error) {
            setError(true);
        }
        setLoading(false);
    }

    // Initial and search
    useEffect(() => {
        setState(initialState);
        fetchProjects(1, searchTerm);
    }, [searchTerm]);

    return { state, loading, error, searchTerm, setSearchTerm };
};


