import React from 'react';
import { Wrapper, Content, ContentHeader, ContentDescription, Image, Title, Description, Type } from './Projects.styles';

const Projects = ({title, image, description, link, type}) => (

    <Wrapper>
        {console.log(link)}
        <a href={link} target="_blank">
            <Content>
                <ContentHeader href={link} target="_blank" image={image}>
                    {/* <Image src={image} alt='project-thumb' /> */}
                    
                    <Type>
                        <p>{type}</p>
                    </Type>
                    <Title><h1>{title}</h1></Title>
                    
                </ContentHeader>
                <ContentDescription>
                    <Description><p>{description}</p></Description>
                </ContentDescription>
            </Content>
            
        </a>
    </Wrapper>
)

export default Projects;