import styled from 'styled-components';

export const Wrapper = styled.div`
    align-items: center;
    top: 0;
    background: var(--nickel);
    z-index:2;
    height:360px;
    position: relative;
    
    overflow:hidden;
`;

export const Content = styled.div`
    align-items: center;
    justify-content: center;
    max-width: var(--maxWidth);
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
`;