import { NOTION_API_KEY, 
    NOTION_PROJECT_DATABASE, 
    PAGE_ID, 
    API_URL
} from "./config"

var myHeaders = new Headers();
myHeaders.append("Notion-Version", "2022-02-22");
myHeaders.append("Authorization", `Bearer ${NOTION_API_KEY}`);
myHeaders.append("Content-Type", "text/plain");

var raw = "{\n    \n}";

var requestOptions = {
  method: 'POST',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};

var id = `${NOTION_PROJECT_DATABASE}`;



const apiSettings = {
    fetchProjects: async() => {
        
        return await (await fetch(`${API_URL}https://api.notion.com/v1/databases/${id}/query`, requestOptions)).json();
    }
    
}

export default apiSettings;