import React from 'react';
//styles
import { Wrapper, Content, Text, Image, ImageContainer} from './TopContent.styles';

import CasualPhoto from '../../images/casual-photo.png';

const TopContent = () =>(
    <Wrapper>
        <Content>
            <ImageContainer>
                <Image src={CasualPhoto} alt='site-logo'/>
            </ImageContainer>
            <Text>
                <h1>Hi!</h1>
                <h2>I'm Arif</h2>
                <p>I love creating and playing games. I have experience with Programming, Designing, 3D Modeling and Animation <br/> <br/>
                I have created various applications for Mobile, VR, PC, and the Web. You can feel free to check out some of them below</p>
            </Text>
        </Content>
        
    </Wrapper>
);

export default TopContent;