import React from 'react';

import TopContent from './TopContent';
import UniqueProjects from './UniqueProjects';
import Grid from './Grid';
import Projects from './Projects';
import Links from './Links';

// import { useProjectsFetch, projects } from '../hooks/useProjectsFetch';
import { useNotionFetch } from '../hooks/useNotionFetch';



function Home(){
    // const { state, loading, error } = useProjectsFetch();

    const { state, loading, error } = useNotionFetch();


    return(
        
        <>
        <TopContent />
        <Links />
        {console.log(state.results)}
        {
        /* <Grid header={'My Projects'}>
            {projects.map(project => (
                <Projects
                    title = {project.Title}
                    description = {project.Description}
                    link = {project.Links}
                    image = {project.Thumbnail}
                    type = {project.Type}
                />
            ))}
        </Grid> */}
        
        <Grid header={'My Projects'}>
            {state.results.map(project => (
                <Projects
                    title = {project.properties.Name.title[0]?.plain_text}
                    description = {project.properties.Description.rich_text[0]?.plain_text}
                    link = {project.properties.Link.url}
                    image = {project.properties.Images.files[0]?.file?.url}
                    type = {project.properties.Type.select?.name}
                />
            ))}
        </Grid>
        <UniqueProjects />
        </>
    )
}

export default Home;