import { Component } from 'react';
import styled from 'styled-components';

export const Wrapper = styled.div`
    //background: var(--darkGrey);    
    padding: 10px 10px;
    overflow: hidden;
    max-height: 100%;
    z-index:100;
    a {
        text-decoration: none;
    }
    
    
`;

export const Content = styled.div`
    position: relative;
    height: 200px;
    max-width: 720px;
    border-radius: 5px;
    
`;

export const ContentHeader = styled.div`
    background: linear-gradient(
        to bottom, 
        rgba(255,255,255,0) 20%, 
        rgba(255,255,255,0.0) 80%, 
        rgba(255,255,255,0.5) 100%
    ), url(${({image}) => image}), var(--mint-cream); 
    height: 200px;
    max-width: 720px;
    background-size: 150%;
    background-position: center;
    border-radius: 5px;
    transition: all 0.3s;
    animation: animateThumb 0.5s;
    box-shadow: 0 0 10px rgba(0,0,0,0.4);
    position:relative;

    ${Content}:hover & {
        height: 40% ;
        background-position: center;
        background-size: 175%;
        opacity:  0.8;
    }

    @keyframes animateThumb{
        from{
            opacity:  0;
        }
        to{
            opacity: 1;
        }
    }
`;

export const ContentDescription = styled.div`
    position: relative;
    border-radius: 5px;
    background-color: var(--beau-blue);
    box-shadow: 0 0 10px rgba(0,0,0,0.4);
    height: 65%;
    margin: 10px, auto;
    width: 95%;
    left: 2.5%;
    top: -200px;
    z-index: -200;
    transition: all 0.3s;

    ${Content}:hover & {
        opacity:  0.8;
        top: -5%;
    }
`;

export const Image = styled.img`
    max-width: 720px;
    transition: all 0.3s;
    border-radius: 5px;
    object-fit: cover;
    animation: animateThumb 0.5s;
    width:100%;
    margin:auto;

    ${Content}:hover & {
        width: 105%;
        opacity:  0.8;
    }

    @keyframes animateThumb{
        from{
            opacity:  0;
        }
        to{
            opacity: 1;
        }
    }
`;

export const Title = styled.div`
    position: absolute;
    width:100%;
    bottom: 0%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%) ;
    margin: 0 auto;
    text-align: center;
    transition: all 0.3s;

    ${Content}:hover & {
        bottom: 50%;
        transform: translate(-50%, 50%);
    }

    h1{
        font-family: verdana;
        font-size: 35px;
        color: rgb(255, 255, 255);
        text-shadow: rgb(0, 0, 0) 0px 3px 12px;
        font-weight: 500;
    }
    
`;

export const Type = styled.div`
    height: 12%;
    position:relative;
    top:0px;
    left:-7px;
    z-index:150;
    

    p {
        font-family: verdana;
        padding:8px;
        display: inline-flex;
        background-color: var(--white);
        color: rgb(0, 0, 0);
        font-size: 15px;
        border-radius: 5px;
    }

    ${Content}:hover & {
        display: none;
    }
`;

export const Description = styled.div`
    position:relative;
    top: 0%;
    color: var(--black);
    padding: 10px 20px;
    display: none;
    transition: all 0.3s;
    animation: slide-top 0.5s cubic-bezier(.1,.72,.39,.96) both;
    text-align:center;

    ${Content}:hover & {
        display: block;
        position: absolute;
        bottom: 0;
    }
    p{
        color: var(--black);
    }

    @keyframes slide-top {
    0% {
        transform: translateY(-30px);
        opacity: 0;
    }
    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}
`;

export const Gap = styled.div`
    height:10px;
`;