import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
 :root { 
     --maxWidth: 1280px;
     --white: #fff;
     --black: #000;
     --lightGrey: #eee;
     --medGrey: #353535;
     --darkGrey: #1c1c1c;
     --beau-blue: #C5D9E7ff;
    --black-chocolate: #201A15ff;
    --mint-cream: #ECF2ECff;
    --nickel: #6A716Dff;
    --dark-jungle: #232823ff;
    --air-blue: #7398ADff;
     --fontVeryBig: 2.5rem;
     --fontBig: 1.5rem;
     --fontMed: 1.2rem;
     --fontSmall: 1rem;
 }

 *{
     box-sizing: border-box;
     font-family: 'Abel', sans-serif;
 }

 body {
     margin: 0;
     padding: 0;

     h1 {
         font-size: 2rem;
         font-weight: 600;
         color: var(--white);
     }

     h3 {
         font-size: 1.1rem;
         font-weight: 600;
     }

     p {
         font-size: 1rem;
         color: var(--white);
     }
 }

`;